.about-us {
  padding-top: 34px;
}

.about-us__wrapper {
  @include wrapper;
}

.about-us__title {
  @include font-text($OpenSans, 25px, 30px, 700);
  color: $black;
  margin: 0;
}

.about-us__description {
  @include font-text($OpenSans, 17px, 20px, 700);
  color: $black;
  margin-top: 88px;
  max-width: 251px;
  position: relative;

  & svg {
    position: absolute;
    top: -48px;
    fill: $seafoam-blue;
  }
}

.about-us__advantages {
  @include list-reset;

}

@media(min-width:$tablet-width) {


  .about-us__title {
    @include font-text($OpenSans, 43px, 50px, 700);
  }

  .about-us__description {
    @include font-text($OpenSans, 20px, 22px, 700);
    max-width: 295px;

    &--flower {
      order: 1;
    }

    &--money {
      order: 3;
    }

    &--heart {
      order: 5;
    }

    &--ball {
      order: 2;
    }

    &--roket {
      order: 4;
    }

    &--gift {
      order: 6;
    }

  }



  .about-us__advantages {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    max-width: 670px;
  }
}

@media(min-width:$desktop-width) {
  .about-us__wrapper {
    padding-left: 225px;
  }

  .about-us__advantages {
    width: 750px;
  }

  .about-us__description {
    width: 50%;
  }
}
